import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { firebaseapp } from "../../utils";
import "firebase/storage";

const Container = styled.div`
	display: flex;
	padding: 5vw;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	justify-content: space-around;
	align-items: center;
	background: url("/images/all_circles.svg");
`;

const TextContainer = styled.div`
	padding: 5vw;
`;

const Img = styled.img`
	width: ${props => (props.theme.screens.md ? "90%" : "36vw")};
	padding-bottom: ${props => (props.theme.screens.md ? "8vw" : "0vw")};
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	color: ${props => props.theme.colors.white};
	font-weight: 700;
	font-size: ${props => props.theme.screens.md ? "2.1rem" : "3rem"};

	span {
		color: ${props => props.theme.colors.yellow};
	}
`;

const SubText = styled.p`
	font-family: "Crimson Text", serif;
	font-weight: 300;
	font-size: 1.5rem;
	color: ${props => props.theme.colors.white};
`;

const Icons = styled.div`
	color: white;
	font-size: 2rem;

	a {
		color: white;
	}

	a:hover {
		color: ${props => props.theme.colors.white};
	}

	i {
		padding: 1.5rem 1.5rem 0rem 0rem;
	}
`;

const Home: React.FC = () => {
	const [img, setImg] = useState("");

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("landing_page/logo.png")
			.getDownloadURL()
			.then(url => setImg(url));
	}, []);

	return (
		<Container>
			<TextContainer>
				<Heading>
					The <span>Yellow</span> Filter
				</Heading>
				<SubText>
					<br></br>
					Hello There. Welcome to The Yellow Filter. Art and Lifestyle to you. <br></br>
					<br></br>
					We are here to give a good taste of Art and Lifestyle. We deal in <b>Interior Decorations</b> of Homes,
					Offices, Work Places, Cafes, Hotels, Resturants and etc. We also deal in <b>Fashion Grooming and
					Photography.</b>
				</SubText>
				<Icons>
					<a href="https://www.instagram.com/the.yellow.filter" target="_blank" rel="noopener noreferrer">
						<i className="fab fa-instagram"></i>
					</a>

					<a href="https://www.facebook.com/The-Yellow-Filter-104261981615307">
						<i className="fab fa-facebook"></i>
					</a>
				</Icons>
				<br></br>
			</TextContainer>
			<Img src={img}></Img>
		</Container>
	);
};

export default Home;
