import React, { useEffect, useState } from "react";
import styled from "styled-components";

import { firebaseapp } from "../../utils";
import "firebase/storage";
import "firebase/database";

const OurGoalsContainer = styled.div`
	padding: ${props => (props.theme.screens.sm ? "2rem" : "10vh")};
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	text-align: center;
`;

const Body = styled.p`
	font-family: "Crimson Text", serif;
	font-weight: 400;
	font-size: 1.4rem;
	line-height: 1.6rem;
	text-align: center;
	padding: ${props => (props.theme.screens.md ? "4vw" : "5vw")};
`;

const HR = styled.hr`
	text-align: center;
	height: 1px;
	background-color: black;
	width: ${props => (props.theme.screens.sm ? "30%" : "7%")};
`;

const ImgContainer = styled.div`
	text-align: center;
	img {
		width: ${props => (props.theme.screens.mmd ? "100%" : "40vw")};
	}
`;

const OurGoals: React.FC = () => {
	const [text, setText] = useState("");
	const [textWords, setTextWords] = useState("");
	const [img, setImg] = useState("");

	useEffect(() => {
		firebaseapp
			.database()
			.ref("landing_page/Our Goals")
			.once("value")
			.then(snapshot => setText(snapshot.val()));

		firebaseapp
			.database()
			.ref("landing_page/Words By Owner")
			.once("value")
			.then(snapshot => setTextWords(snapshot.val()));

		firebaseapp
			.storage()
			.ref("landing_page/owner_pic.jpg")
			.getDownloadURL()
			.then(url => setImg(url));
	});

	const getHTML = () => {
		return { __html: text };
	};

	const getHTMLWords = () => {
		return { __html: textWords };
	};

	return (
		<OurGoalsContainer>
			<Heading>Our Goals</Heading>
			<HR></HR>
			<Body dangerouslySetInnerHTML={getHTML()}></Body>

			<br></br>

			<Heading>Some Words by our Owner</Heading>
			<HR></HR>
			<Body dangerouslySetInnerHTML={getHTMLWords()}></Body>

			<ImgContainer>
				<img src={img}></img>
			</ImgContainer>
		</OurGoalsContainer>
	);
};

export default OurGoals;